import * as React from "react";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";

export default function Dropdown({ data, label, handleData }) {
  const [current, setCurrent] = React.useState("");

  const handleChange = (event) => {
    setCurrent(event.target.value);
    handleData(event.target.value);
  };
  return (
    <FormControl variant="standard" sx={{ m: 1, minWidth: 200 }} size="small">
      <InputLabel id="demo-simple-select-standard-label">{label}</InputLabel>
      <Select value={current} onChange={handleChange} label={label}>
        {data?.map((ele, idx) => (
          <MenuItem value={ele} key={idx}>
            {ele ? ele : null}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
}
